.footer-container {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 20px;
    background-color: var(--secondary-color);
    color: var(--primary-color);
    z-index: 1;
    background-color: rgba(76, 96, 105, 0.8);
}

.footer-text{
    font-family: 'Saira Extra Condensed', sans-serif;
    font-weight: 500;
    font-size: 20px;
    letter-spacing: 3px;
    color: white;
    padding:0px;
    margin:0px;
    margin-left:150px;
}
